main {
  position: relative;
}

.grid-overlay {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 2;
  overflow: hidden;
}

/*
  * Getting the proportions correct and allowing enough rows to cover
  * excess height
  *
  * To get to 296px width the parent container (grid overlay) must be at a
  * width of 1256px, with for columns with a gutter of 24px.
  *
  * In the case of the rows, 16rem is as near as I can get using a rem
  * unit to the designed unit of 296px
  */
.grid-visible .master-grid {
  width: 100%;
  pointer-events: none;
  grid-template-rows: repeat(30, 11.5rem);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem 2rem;
}

@media (min-width: 1332px) {
  .grid-visible .master-grid {
    grid-template-rows: repeat(30, 24rem);
    grid-gap: 2rem 3rem;
  }
}

.grid-visible .master-grid > div {
  border: 1px solid;
}

@media (min-width: 978px) {
  .grid-visible .master-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1332px) {
  .grid-visible .master-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 1656px) {
  .grid-visible .master-grid {
    grid-template-columns: repeat(5, 1fr);
  }
}

body {
  counter-reset: default-counter;
}

h1 {
  grid-column: 1 / span 2;
  align-self: end;
}

@media (min-width: 978px) {
  h1 {
    grid-row: 2;
    grid-column: 2 / span 2;
  }
}

@media (min-width: 1332px) {
  h1 {
    grid-row: 1;
    grid-column: 3 / span 2;
  }
}

main {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 1rem;
}

@media (min-width: 1332px) {
  main {
    grid-row-gap: 2rem;
  }
}

@media (min-width: 648px) and (max-width: 977px) {
  main {
    grid-template-rows: auto;
  }
}

section {
  display: grid;
  grid-gap: 1rem 2rem;
  grid-auto-rows: minmax(11.5rem, auto);
}

@media (min-width: 1332px) {
  section {
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2rem 3rem;
  }
}

@media (min-width: 1656px) {
  section {
    grid-template-columns: repeat(5, 1fr);
  }
}

@media (min-width: 978px) {
  .preamble {
    grid-template-rows: repeat(4, minmax(11.5rem, auto));
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1332px) {
  .preamble {
    grid-template-rows: repeat(2, minmax(24rem, auto));
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 1656px) {
  .preamble {
    grid-template-columns: repeat(5, 1fr);
  }
}

.preamble .intro {
  margin-top: -1rem;
}

@media (min-width: 648px) {
  .preamble .intro {
    grid-row: 2 / span 2;
  }
}

@media (min-width: 978px) {
  .preamble .intro {
    grid-row: 3 / span 2;
  }
}

@media (min-width: 978px) {
  .preamble .intro {
    grid-column: 2 / span 2;
  }
}

@media (min-width: 1332px) {
  .preamble .intro {
    grid-row: 2;
    grid-column: 3 / span 2;
  }
}

.design-principles {
  grid-template-columns: 1fr 1fr;
}

@media (min-width: 648px) {
  .design-principles {
    grid-template-rows: repeat(9, minmax(11.5rem, auto));
  }
}

@media (min-width: 978px) {
  .design-principles {
    grid-template-rows: repeat(6, minmax(11.5rem, auto));
  }
}

@media (min-width: 1332px) {
  .design-principles {
    grid-template-rows: repeat(2, minmax(24rem, auto));
  }
}

@media (min-width: 540px) and (max-width: 647px) {
  .design-principles .section-info {
    grid-row: span 1;
  }
  .design-principles .section-info:nth-of-type(2) {
    grid-row: 6 / span 1;
  }
  .design-principles .section-info:nth-of-type(2) ~ .image-link {
    grid-row: 7 / span 2;
  }
}

@media (min-width: 648px) and (max-width: 977px) {
  .design-principles .section-info {
    grid-row: span 1;
  }
  .design-principles .section-info:nth-of-type(2) {
    grid-row: 7 / span 1;
  }
  .design-principles .section-info:nth-of-type(2) ~ .image-link {
    grid-row: 8 / span 2;
  }
}

@media (min-width: 978px) and (max-width: 1331px) {
  .design-principles .section-info {
    grid-row: span 1;
  }
  .design-principles .section-info:nth-of-type(2) {
    grid-row: 6 / span 1;
  }
  .design-principles .section-info:nth-of-type(2) ~ .image-link {
    grid-row: 6 / span 2;
  }
}

@media (min-width: 1332px) and (max-width: 1655px) {
  .design-principles .section-info:nth-of-type(2) {
    grid-row: 3;
  }
  .design-principles .section-info:nth-of-type(2) ~ .image-link {
    grid-row: 3;
  }
}

@media (min-width: 540px) {
  .design-principles {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 540px) and (max-width: 977px) {
  .design-principles .image-link:nth-child(2) {
    grid-column-start: 1;
  }
}

@media (min-width: 978px) {
  .design-principles {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 978px) and (max-width: 1331px) {
  .design-principles .image-link:nth-child(4) {
    grid-column-start: 2;
  }
}

@media (min-width: 1332px) {
  .design-principles {
    grid-column: span 4;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2rem 3rem;
  }
}

@media (min-width: 1656px) {
  .design-principles {
    grid-column: span 5;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 24rem;
  }
}

.image-link {
  display: grid;
  grid-template-rows: auto 1fr;
  text-decoration: none;
}

@media (min-width: 540px) and (max-width: 1331px) {
  .image-link {
    grid-row: span 2;
  }
}

@media (max-width: 539px) {
  .image-link {
    grid-template-columns: repeat(2, 1fr);
    grid-column: span 2;
    grid-gap: 1rem 2rem;
  }
}

.image-link span {
  display: grid;
  align-items: baseline;
  grid-template-columns: auto 1fr;
}

.section-info {
  display: grid;
  align-content: flex-start;
}

@media (max-width: 647px) {
  .section-info {
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 1rem 2rem;
  }
}

@media (max-width: 539px) {
  .section-info {
    grid-row: span 2;
    grid-column: span 2;
  }
}

.section-info h5 {
  margin-top: -1rem;
}

@media (max-width: 647px) {
  .section-info h5 {
    align-self: flex-end;
  }
}

@media (max-width: 539px) {
  .image-link > img {
    order: 2;
  }
}

@media (min-width: 1332px) and (max-width: 1655px) {
  .image-link:nth-of-type(4) {
    grid-column: 2;
  }
}

